import { postParamJSON } from '@/utils/request';

// 保存某个页面的自定义列配置
export const saveCustomColumnSettingAPI = (query, body) => postParamJSON('/configure/pageUserCustom/savePageUserCustomInfo.action?', query, body);

// 获取某个页面的自定义列配置
export const getCustomColumnSettingAPI = (query, body) => postParamJSON('/configure/pageUserCustom/getPageUserCustomDetails.action?', query, body);
