// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage, showErrorMessage, showSuccessMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import SearchBox from '@/components/common/SearchBox';
import DialogSettingColumn from '@/components/common/DialogCustomizeColumns/DialogSettingColumn';
import {
  STATUS_CODE,
  TITLE,
  VARIABLE,
  BTN_TEXT,
  EQUIPMENT_LIVE,
  DEVICE_STATE,
  CONSTANTS_DEVICE_CONFIGURATION
} from '@/utils/constantsFile';
// 导入所需接口
import {
  deviceGroupGetTreeFunc,
  deviceWorkshopTypeGetTreeFunc,
  mixDefaultAndCustomColumns,
  searchCondetionFunc
} from '@/utils/publicFunction';
import { GetPageByDevice, GetListQueryParameter } from '@/api/equipmentstatus';
// import { ExportDeviceLiveExcel } from '@/api/export';
import CustomColumn from '@/components/common/CustomColumn';
import { getUrlKey } from '@/utils/common';
// 导入自定义列配置有关接口
import {
  // 保存自定义列配置
  saveCustomColumnSettingAPI,
  // 获取自定义列配置
  getCustomColumnSettingAPI
} from '@/api/customizeColumn';
// import tableChange from '@/views/equipment/EquipmentStatus/tableChange.vue';
export default {
  components: {
    // 加载通用table组件
    VXETable,
    SearchBox,
    CustomColumn,
    DialogSettingColumn
    // tableChange
  },
  props: {
    groupType: String,
    isSearchBoxShow: {
      type: Boolean
    },
    showInfo: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      VARIABLE: VARIABLE,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      BTN_TEXT: BTN_TEXT,
      deviceName: '',
      dialogVisible: false,
      hackReset: false,
      // 查询字段
      srearchList: [],
      orderByData: {},
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // 查询字段
      searchData: [],
      // 显示那个表
      showTable: true,
      // table是否加载
      tableLoadings: false,
      // 控制自定义列模态框显示
      dialogVisibleCustomColumn: false,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumn: [],
      tableColumns: [
        {
          type: 'radio',
          align: 'center',
          visible: true,
          width: '50',
          fixed: 'left'
        },
        {
          field: 'factory',
          title: '工厂',
          visible: true,
          minWidth: '150',
          align: 'center'
        },
        {
          field: 'workshop',
          title: '车间',
          visible: true,
          minWidth: '150',
          align: 'center'
        },
        {
          field: 'productionLine',
          title: '产线',
          visible: true,
          minWidth: '150',
          align: 'center'
        },
        {
          field: 'deviceName',
          title: EQUIPMENT_LIVE.DEVICE_NAME,
          visible: true,
          sortable: true,
          minWidth: '150',
          align: 'center'
        },
        {
          field: 'deviceCode',
          visible: true,
          sortable: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_CODE,
          minWidth: '150',
          align: 'center'
        },
        {
          field: 'devicePriCode',
          visible: true,
          sortable: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_PRCODE,
          minWidth: '200',
          align: 'center'
        },
        {
          field: 'number',
          visible: true,
          sortable: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_EXTEND,
          minWidth: '200',
          align: 'center'
        },
        {
          field: 'typeName',
          title: EQUIPMENT_LIVE.DEVICE_TYPE,
          visible: true,
          minWidth: '150',
          align: 'center'
        },
        // {
        //   field: 'fileUrl',
        //   title: EQUIPMENT_LIVE.DEVICE_IMAGE,
        //   width: '',
        //   align: 'center',
        //   sortable: true,
        //   slots: {
        //     default: ({ row, column, cellValue }) => {
        //       return [
        //         <div v-show={row.fileUrl !== undefined}>
        //           <img class="showImg" src={row.fileUrl} />
        //         </div>
        //       ];
        //     }
        //   }
        // },
        {
          field: 'deviceRealState',
          title: EQUIPMENT_LIVE.DEVICE_STATE,
          visible: true,
          minWidth: '150',
          align: 'center',
          formatter: ({ row }) => {
            return DEVICE_STATE[row.deviceRealState];
          },
          slots: {
            default: ({ row, column }) => {
              if (row.deviceRealState === 1) {
                return [
                  <div>
                    <span class="blue">11</span>
                    {DEVICE_STATE[1]}
                  </div>
                ];
              } else if (row.deviceRealState === 2) {
                return [
                  <div>
                    <span class="yellow">11</span>
                    {DEVICE_STATE[2]}
                  </div>
                ];
              } else if (row.deviceRealState === 3) {
                return [
                  <div>
                    <span class="stop">11</span>
                    {DEVICE_STATE[3]}
                  </div>
                ];
              } else if (row.deviceRealState === 4) {
                return [
                  <div>
                    <span class="red">11</span>
                    {DEVICE_STATE[4]}
                  </div>
                ];
              }
            }
          }
        },
        {
          field: 'todayValue',
          title: EQUIPMENT_LIVE.TODAY_OUTPUT,
          visible: true,
          minWidth: '150',
          align: 'center',
          formatter: ({ row }) => {
            // return DEVICE_STATE[row.deviceRealState];
            if (row.currentShiftName) {
              return row.currentValue;
            } else {
              return row.todayValue;
            }
          }
        },
        {
          field: 'dayCount',
          title: EQUIPMENT_LIVE.TODAY_ALARMS,
          visible: true,
          minWidth: '150',
          align: 'center'
        },
        // {
        //   field: 'maintenanceTime',
        //   title: EQUIPMENT_LIVE.ESTIMATED_MAINTENANCE_TIME,
        //   formatter: ['formatDate', 'yyyy-MM-dd'],
        //   width: '150',
        //   align: 'center',
        //   sortable: true
        // },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          visible: true,
          width: '50',
          align: 'center',
          fixed: 'right',
          // type: 'html',
          slots: {
            header: ({ column }) => {
              return [
                <span>
                  {/* <span>{column.title}</span> */}
                  <i
                    onClick={() => this.showCustomColumn()}
                    class="el-icon-s-tools set-btn"
                  ></i>
                </span>
              ];
            }
            // default: ({ row, column, cellValue }) => {
            //   return [
            //     <div>
            //       <el-button type="text" onClick={() => this.goInfo(row)}>
            //         {BTN_TEXT.INFO}
            //       </el-button>
            //     </div>
            //   ];
            // }
          }
        }
      ],
      typeOptions: [],
      groupOptions: [],
      typeValue: '',
      groupValue: '',
      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      // 保存自定义之后的列配置
      savedCustomColumns: [],
      // 是否显示自定义列对话框
      isSettingCustomDialogShow: false
    };
  },
  computed: {
    // 表格使用的表头配置
    myTableColumns: function() {
      if ((Array.isArray(this.savedCustomColumns)) && this.savedCustomColumns.length > 0) {
        return this.savedCustomColumns;
      } else {
        return this.tableColumns;
      }
    }
  },
  created() {
    this.getCustomColumn(1);
    // 获取查询参数
    this.GetListQueryParameter();
    // 调用列表查询接口
    this.findList();
    // // 类型下拉数据
    // this.getTypeTree();
    // 车间分组下拉数据
    this.getGroupTree();
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      try {
        this.tableLoadings = true;

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: [],
          orderBy: this.orderByData
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        const res = await GetPageByDevice(
          {
            size: this.tablePages.pageSize,
            current: this.tablePages.currentPage
          }, conditionJson
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          for (var i in this.tableDatas) {
            if (this.tableDatas[i].fileUrl !== undefined) {
              this.tableDatas[i].fileUrl =
                this.$store.state.publicUrl +
                this.tableDatas[i].fileUrl.substring(1);
            }
          }
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 获取类型树数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    async getGroupTree() {
      // 若是车间分组
      // 执行调用接口
      try {
        const res = await deviceGroupGetTreeFunc();
        if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
          this.groupOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取设备类型树数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    async getTypeTree() {
      // 若是设备类型
      // 执行调用接口
      try {
        const res = await deviceWorkshopTypeGetTreeFunc();
        if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
          this.typeOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 查询
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    search() {
      this.onTablePageChanges(1, 10);
    },
    /**
     * @description: 重置
     * @param {type}
     * @return:
     * @author: CH
     */
    reset() {
      this.typeValue = '';
      this.groupValue = '';
      this.deviceName = '';
      this.onTablePageChanges(1, this.tablePages.pageSize);
    },
    // 双击某行
    dbclickRows() {
      this.goInfo();
    },
    /**
     * @description: 进入详情页面
     * @param obj ztree对象
     * @return: 无
     *@author: ZJM
     */
    goInfo() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.showInfo(1, row);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      var that = this;
      // try {
      //   const res = await ExportDeviceLiveExcel(
      //     {},
      //     {
      //       conditionJson1: this.searchData.conditionArr1,
      //       operator: this.searchData.groupOperator,
      //       conditionJson2: this.searchData.conditionArr2,
      //       orderBy: this.orderByData
      //     }
      //   );
      //   if (res.data.code !== undefined) {
      //     showWarningMessage(res.message);
      //   } else {
      //     this.$options.filters.setExportFunc(res, '设备实况列表.xls');
      //   }
      // } catch (error) {
      //   showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      // } finally {
      // }
      try {
        this.tableLoadings = true;
        const res = await GetPageByDevice(
          {
            size: 99999999,
            current: this.tablePages.currentPage
          },
          {
            conditionJson1: this.searchData.conditionArr1,
            operator: this.searchData.groupOperator,
            conditionJson2: this.searchData.conditionArr2,
            orderBy: this.orderByData
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          // this.tableDatas = res.rows;
          // for (var i in this.tableDatas) {
          //   if (this.tableDatas[i].fileUrl !== undefined) {
          //     this.tableDatas[i].fileUrl =
          //       this.$store.state.publicUrl +
          //       this.tableDatas[i].fileUrl.substring(1);
          //   }
          // }
          // 将总条数写入table分页配置
          // this.tablePages.total = res.total;

          var customColumns = [];

          // 获取最新表列数据
          if ((Array.isArray(that.savedCustomColumns)) && that.savedCustomColumns.length > 0) {
            customColumns = that.savedCustomColumns;
          } else {
            customColumns = that.tableColumns;
          }

          // 过滤需要展示的列
          var showArr = [];
          for (var i = 0; i < customColumns.length; i++) {
            if (customColumns[i].type !== 'radio' && customColumns[i].type !== 'checkbox') {
              showArr.push(customColumns[i].field);
            }
          }

          this.$refs.xGrid.exportData('设备实况', res.rows, showArr);
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:表格排序
     * @return:
     * @author: CH
     */
    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      this.onTablePageChanges(1, this.tablePages.pageSize);
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: CH
     */
    changeTable() {
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.hackReset = true; // 重建组件
      });
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChanges(1, this.tablePages.pageSize);
    },

    /**
     * @description:关闭弹窗
     * @param {type}
     * @return:
     * @author: CH
     */
    handleClose() {
      this.dialogVisible = false;
      this.hackReset = false;
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async GetListQueryParameter() {
      try {
        const res = await GetListQueryParameter({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.srearchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 切换显示自定义列模态框
     * @param 无
     * @return: void
     * @author: ZJM
     */
    showCustomColumn() {
      this.isSettingCustomDialogShow = !this.isSettingCustomDialogShow;
    },
    // /**
    //  * @description: 保存自定义列
    //  * @param 无
    //  * @return: void
    //  * @author: ZJM
    //  */
    // saveCustomColumn(columns) {
    //   this.tableColumns = columns;
    //   this.showCustomColumn();
    // },
    // /**
    //  * @description: 恢复默认自定义列
    //  * @param 无
    //  * @return: void
    //  * @author: ZJM
    //  */
    // resetCustomColumn() {
    //   this.showCustomColumn();
    //   // 按时间从大到小
    //   this.tableColumns = this.tableColumns.sort((a, b) => {
    //     return a.zIndex > b.zIndex ? 1 : -1;
    //   });
    //   this.tableColumns = this.tableColumns.map((item) => {
    //     item.width = item.yWidth;
    //     item.visible = item.yVisible;
    //     return item;
    //   });
    // },
    /**
     * @description: (自定义列对话框的自定义事件) 点击“保存”按钮，调用接口
     * @param {number} cssType 同一路由下，区分不同页面的标志
     * @return promise
     * @author: cyb
     */
    async handleSaveColumnSetting(submitColumnSetting, cssType = this.cssType) {
      const loading = this.$loading({
        body: true,
        lock: true,
        text: '自定列配置提交中...'
      });

      try {
        // 获取路由的id
        // const { id } = await getMenuIdForUrlAPI({ url: this.$route.path });
        const id = getUrlKey('id');

        // 获取用户名
        const account = window.localStorage.getItem('account').replace(/"/g, '');
        const header = {
          account,
          cssType: 1,
          menuId: id
        };

        const res = await saveCustomColumnSettingAPI(
          header,
          submitColumnSetting
        );

        if (res.code === '0000') {
          loading.close();
          showSuccessMessage(res.message);
          this.refreshTableColumns();
        } else {
          loading.close();
          showErrorMessage(res.message || '操作失败！');
        }
      } catch (error) {
        loading.close();
        showErrorMessage(error + '666');
      } finally {
        // 关闭对话框
        this.isSettingCustomDialogShow = false;
      }
    },
    /**
     * @description: 获取自定义列配置
     * @param 无
     * @return void
     * @author: cyb
     */
    async getCustomColumn(cssType) {
      try {
        // 调接口获取当前页面的menuId
        // const urlInfo = await getMenuIdForUrlAPI({ url: this.$route.path });
        const id = getUrlKey('id');
        // 获取用户名
        const account = window.localStorage.getItem('account').replace(/"/g, '');
        const query = {
          account,
          cssType: cssType,
          menuId: id
        };
        const res = await getCustomColumnSettingAPI(query);
        // 如果有返回配置，则将默认表头和自定义配置混合
        if (Array.isArray(res) && res.length > 0) {
          const arr = mixDefaultAndCustomColumns(res, this.tableColumns);
          if (this.savedCustomColumns.length > 0) {
            this.savedCustomColumns.splice(0);
          }
          this.savedCustomColumns.push(...arr);
        }
      } catch (error) {
        showErrorMessage(error);
      }
    },
    /**
     * @description: 刷新表格表头和表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableColumns() {
      this.getCustomColumn(1);
      // 刷新表格
      this.refreshTableData();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.tablePages.currentPage = 1;
      }
      this.findList();
    }
  }
};
