import VXETable from '@/components/common/Table.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 导入所需接口
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_LIVE,
  MSG
} from '@/utils/constantsFile';
import { List } from 'vxe-table';
import {
  GetPageHamsterInformation,
  // GetBrokenLine,
  getTimeDeviceParameterReport
} from '@/api/equipmentstatus';
export default {
  name: 'echarts',
  props: {
    deviceData: List,
    lineTime: List
    // dateTimes: String
  },
  data() {
    return {
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      TITLE: TITLE,
      BTN_TEXT: BTN_TEXT,
      lineTime1: '',
      lineTime2: [],
      showFlag: 0,
      active: 0,
      relationshipData: [],
      relationshipId: [],
      tabs: [
        {
          name: '列表',
          value: 0
        },
        {
          name: '图表',
          value: 1
        }
      ],
      pickerOptions: {
        // disabledDate(time) {
        //   return time.getTime() > Date.now();
        // },
        onPick: (time) => {
          // 当第一时间选中才设置禁用
          if (time.minDate && !time.maxDate) {
            this.timeOptionRange = time.minDate;
          }
          if (time.maxDate) {
            this.timeOptionRange = null;
          }
        }
        // disabledDate: (time) => {
        //   var timeOptionRange = this.timeOptionRange;
        //   var secondNum = 60 * 60 * 1000 * 12;
        //   if (timeOptionRange) {
        //     if (timeOptionRange.getTime() + secondNum > Date.now() - 8.64e6) {
        //       return (
        //         time.getTime() > Date.now() - 8.64e6 ||
        //         time.getTime() < timeOptionRange.getTime() - secondNum
        //       );
        //     }
        //     return (
        //       time.getTime() > timeOptionRange.getTime() + secondNum ||
        //       time.getTime() < timeOptionRange.getTime() - secondNum
        //     );
        //   } else {
        //     return time.getTime() > Date.now();
        //   }
        // }
      },
      parameterName: '',
      paramOptions: [],
      paramSelect: null,
      tableLoading: false,
      tableColumns: [],
      tableDatas: [],
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  watch: {
    relationshipId(val) {
      if (val.length > 3) {
        showWarningMessage(MSG.ONLY_SELECT_THREE);
        val.pop();
      } else {
        this.showFlag = true;
        this.showEcharts();
        this.showTable();
      }
    },
    lineTime(val) {
    }
  },
  created() {
    this.lineTime1 = this.$utils.toDateString(
      new Date(),
      'yyyy-MM-dd'
    );

    var hour = new Date().getHours();
    var hour2 = '';
    if (hour < 10) {
      hour = '0' + hour;
      if (hour === 0) {
        hour2 = '00';
      } else {
        hour2 = '00';
      }
    } else {
      if (hour < 12) {
        hour2 = '';
      } else {
        hour2 = (hour - 12);
        hour2 = hour2 < 10 ? '0' + hour2 : hour2;
      }
    }

    this.lineTime2 = [hour2 + ':00:00', hour + ':00:00'];

    // 调用列表查询接口
    this.GetHamsterInformation();
    // this.$nextTick(function() {
    //   this.showEcharts();
    // });
    // this.showTable();
  },
  mounted() {},
  methods: {
    toggle(i, v) {
      this.showFlag = i;
      this.active = i;
      if (this.paramSelect.length === 0) return;
      if (i === 0) {
        this.showTable();
      }
      if (i === 1) {
        this.showEcharts();
      }
    },
    async showTable(param) {
      try {
        this.tableLoading = true;

        var paramJson = {
          deviceCode: this.deviceData.deviceCode,
          startTime: this.lineTime1 + ' ' + this.lineTime2[0],
          endTime: this.lineTime1 + ' ' + this.lineTime2[1]
        };

        if (this.paramSelect) {
          paramJson.relationshipIds = this.paramSelect.join(',');
        }

        const res = await getTimeDeviceParameterReport(paramJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入选项
          var data = res.rows;
          this.tableColumns = [];
          this.tableDatas = [];
          var devName = '';
          this.tableColumns.push({
            field: 'time',
            title: '时间',
            visible: true,
            width: '200',
            align: 'center'
          });
          for (var i in data.parameterSnapshotMap) {
            devName = data.parameterSnapshotMap[i].deviceName;

            var parameterName = data.parameterSnapshotMap[i].parameterName;
            if (data.parameterSnapshotMap[i].showUnit) {
              parameterName = parameterName + '(' + data.parameterSnapshotMap[i].showUnit + ')';
            }

            this.tableColumns.push({
              field: i + '_param',
              title: parameterName,
              minWidth: '100',
              visible: true,
              align: 'center'
            });
            for (var x in data.deviceParameterRecord[i]) {
              if (this.tableDatas[x]) {
                this.tableDatas[x][i + '_param'] = data.deviceParameterRecord[i][x].value;
              } else {
                this.tableDatas.push({
                  time: data.deviceParameterRecord[i][x].time
                });
                // console.log(this.tableDatas);
                this.tableDatas[x][i + '_param'] = data.deviceParameterRecord[i][x].value;
              }
            }
          }
          if (param === 'export') {
            this.$refs.xGrid.exportDatax(devName + '设备参数报表', this.tableDatas);
          }
          this.tableLoading = false;
        } else {
          this.tableLoading = false;
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:展示图形
     * @param {type}
     * @return:
     * @author: CH
     */
    async showEcharts() {
      try {
        this.tableLoading = true;

        var paramJson = {
          deviceCode: this.deviceData.deviceCode,
          startTime: this.lineTime1 + ' ' + this.lineTime2[0],
          endTime: this.lineTime1 + ' ' + this.lineTime2[1]
        };
        if (this.paramSelect.length > 0) {
          paramJson.relationshipIds = this.paramSelect.join(',');
          const res = await getTimeDeviceParameterReport(paramJson);
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 将请求数据写入选项
            var data = res.rows;
            this.tableColumns = [];
            this.tableDatas = [];
            var series = [];
            for (var i in data.parameterSnapshotMap) {
              var valueData = [];
              var parameterName = data.parameterSnapshotMap[i].parameterName;
              if (data.parameterSnapshotMap[i].showUnit) {
                parameterName = parameterName + '(' + data.parameterSnapshotMap[i].showUnit + ')';
              }
              for (var x in data.deviceParameterRecord[i]) {
                valueData.push(data.deviceParameterRecord[i][x].value);
              }
              series.push({
                name: parameterName,
                type: 'line',
                data: valueData
              });
            }
            this.showEchartsData(res.rows.recordTime, series);
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
      // try {
      //   const res = await GetBrokenLine({
      //     deviceCode: this.deviceData.deviceCode,
      //     relationshipId: this.relationshipId.toString(),
      //     startTime: this.lineTime[0],
      //     endTime: this.lineTime[1]
      //   });
      //   if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
      //     // 将请求数据写入选项

      //     var data = res.rows;
      //     var series = [];
      //     for (var i in data.values) {
      //       series.push({
      //         name: data.values[i].sbcsName,
      //         type: 'line',
      //         data: data.values[i].value
      //       });
      //     }
      //     this.showEchartsData(data, series);
      //   } else {
      //     // 提示异常信息
      //     showWarningMessage(res.message);
      //   }
      // } catch (error) {
      //   showErrorNotify(TITLE.NOTIFY, error);
      // } finally {
      // }
    },
    showEchartsData(data, series) {
      var myChart = this.$echarts.init(
        document.getElementById('showEcharts')
      );

      myChart.setOption({
        title: {
          text: EQUIPMENT_LIVE.PARAMETER_LINE_CHART
        },
        tooltip: {
          trigger: 'axis'
        },
        toolbox: {
          // feature: {
          //   saveAsImage: {}
          // }
        },
        dataZoom: [
          {
            show: true,
            realtime: true,
            start: 65,
            end: 85
          },
          {
            type: 'inside',
            realtime: true,
            start: 65,
            end: 85
          }
        ],
        grid: {
          left: '4%',
          right: '6%',
          bottom: '8%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          name: EQUIPMENT_LIVE.TIME,
          boundaryGap: false,
          data: data
        },
        yAxis: {
          type: 'value',
          name: EQUIPMENT_LIVE.PARAMETER_VALUE
        },
        series: series
      }, true);
    },
    /**
     * @description:查询设备
     * @param{row}
     * @return:{json}
     * @author: CH
     */
    async GetHamsterInformation() {
      try {
        this.tableLoadings = false;
        const res = await GetPageHamsterInformation({
          size: 100000,
          current: 1,
          deviceCode: this.deviceData.deviceCode,
          time: ''
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.relationshipData = res.rows.parameter;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    searchLine() {
      // this.$refs.showLine.showEcharts();
      // this.showTable();
      if (this.active === 0) {
        this.showTable();
      }
      if (this.active === 1) {
        this.showEcharts();
      }
    },
    exprotLine() {
      this.showTable('export');
    }
  },
  components: {
    VXETable
  }
};
